<template>
  <h1>Facturar  ---------------------- {{getSuma}}</h1>
  <hr/>
  <div v-if="getEditFactura">
    <EditFactura />
  </div>
  <div v-else class="row">
      <FormFacturar class="col-sm-12 col-md-6 col-lg-3 col-xl-3 mt-5" />
      <TablaFacturas class="col-sm-12 col-md-6 col-lg-9 col-xl-9 mt-3" />
  </div>
  
</template>

<script>
import { defineAsyncComponent } from 'vue';
import { mapGetters } from 'vuex';

export default {
    components: {
        FormFacturar: defineAsyncComponent(() => import("@/modules/socios/components/Facturar/FormFacturar")),
        TablaFacturas: defineAsyncComponent(() => import("@/modules/socios/components/Facturar/TablaFacturas")),
        EditFactura: defineAsyncComponent(() => import("@/modules/socios/components/Facturar/EditFactura")),
    },
    computed:{
        ...mapGetters('facturar',['getEditFactura','getSuma'])
    }
}
</script>

